html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background: url(https://wallpapercave.com/wp/amnLOJw.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

img.d-block.w-50 {
  width: 300px;
  height:350px;
  float:left;
  margin-left: 30px;
}
div.carousel.slide{
  float: left;
}
div.carousel-inner{
  float: left;
}
h2.card-title{
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}
h3{
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  font-size: 14pt;
}
button.btn-success {
  font-family: Arial, Helvetica, sans-serif;
  color: black;
}
.App-link {
  color:blue;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
#header{
  background: url(data:image/jpeg;base64,);
  height: 175px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.mdl-js{
  background: url(https://lh5.googleusercontent.com/-pKk3f5Yzbjg/VMK7lnFA3BI/AAAAAAAAAuA/4GS-VIyX1gk/s1600/pikachu-pokemon-wallpaper-9397-wallpaper-wallhud.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  min-height: 100vh;
}


#title{
    color: whitesmoke;
    font-family: Arial, Helvetica, sans-serif;
    font-weight:bold;
    font-size:24pt;
}
.delete-btn {
    float: left;
    color: #e74944;
  }
  
